/* ===============================
   Point 1: Card badges (pills)
   =============================== */

.cb-card-badges {
  display: flex;
  flex-wrap: wrap;
  gap: 0.45rem;
  margin: 0 0 0.55rem 0;
}

/* Base badge */
.cb-badge {
  display: inline-flex;
  align-items: center;

  font-size: 0.70rem;
  line-height: 1;
  padding: 0.30rem 0.55rem;
  border-radius: 999px;

  /* Cool cyan tone (normal badges) */
  background: rgba(80, 170, 255, 0.22);
  border: 1px solid rgba(80, 170, 255, 0.80);
  color: #eaf4ff;

  letter-spacing: 0.25px;
  white-space: nowrap;
}

/* Context badges (non-first, non-ROI) slightly softer */
.cb-card-badges .cb-badge:not([data-type="roi"]):not(:first-child) {
  background: rgba(80, 170, 255, 0.14);
  border-color: rgba(80, 170, 255, 0.55);
  color: #dbeaff;
}

/* Bright ROI badge */
.cb-badge[data-type="roi"] {
  background: rgba(0, 255, 140, 0.25);
  border-color: rgba(0, 255, 140, 0.85);
  color: #eafff5;
  font-weight: 600;
}

/* ===============================
   Point 2: card preview/hook should not be blue
   =============================== */

#post-list a.post-preview .post-content,
#post-list a.post-preview .cb-card-hook {
  color: rgba(255, 255, 255, 0.72) !important;
}

#post-list a.post-preview .post-content a,
#post-list a.post-preview .cb-card-hook a {
  color: inherit !important;
  text-decoration: none !important;
}

